// =================== Bootstrap ====================

$grid-gutter-width: 30px;
$grid-columns: 12;
$grid-breakpoints: (xs: 0,
  sm: 576px,
  md: 768px,
  lg: 992px,
  xl: 1200px);
$container-max-widths: (sm: 540px,
  md: 720px,
  lg: 960px,
  xl: 1170px);

// ==================== FONTS =======================

$main-font: Arial,
Tahoma,
sans-serif;

$ExoRegular: 'Exo',
$main-font;


// =================== COLORS =======================

$black: #000000;
$white: #ffffff;
$grey: #999999;
